:root{
    /* --mainColor: #8B008B; */
    --mainColor: #cd3b3b;
}

header{
    background-color: var(--mainColor);
    /* height: 20vh; */
}

.container{
    width: 95%;
    /* background-color: orange; */
    margin: 0 auto;
    max-width: 1200px;
}


.container-flex{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo{
    width: 100px;
    padding: .5em 0;
    border-radius: 50%;
}

.list{
    display: flex;
}

.listItem{
    margin-left: 2em;
    color: #fff;
    text-decoration: none;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
}

.active{
    color: orange;
    border-bottom: .1em double #fff;
}

.icon{
    color:#fff;
    font-size: 1.5rem;
    margin-right: 1rem;
    margin-left: 1.5em;
    justify-content: space-between;
}

/*    media query      */

@media screen and (max-width:1100px){
    .icon{
        margin-left: 1em;
    }
}

@media screen and (max-width: 768px){
    html{
        font-size: 13px;
    }

    .container-flex{
        flex-direction: column;
    }

    .logo{
        width: 150px;
    }
    .icon{
        margin: 1em 1.5em;
    }
}

@media screen and (max-width: 568px){
    html{
        font-size: 11px;
    }
    .list{
        flex-direction: column;
    }
    .listItem{
        margin-top: .2em;
    }
    .logo{
        width: 50px;
    }
}